@-webkit-keyframes MainMenu_redGlitch__24SW_ {
  0%   {color: red; text-shadow: 0px 3px 0px red; }
  25%  {color: black; text-shadow: 6px 2px 1px red;}
  35%  {color: green; text-shadow: 6px 2px 1px red;}
  40%  {color: red; text-shadow: 3px -3px 2px red; }
  50%  {color: blue; text-shadow: -10px -3px -2px black; }
  65%  {color: red; text-shadow: 4px 7px -0px black; }
  75%  {color: green; text-shadow: -2px 5px 1px black;}
  100% {color: red; text-shadow: 0px 0px 0px red; }
}

@keyframes MainMenu_redGlitch__24SW_ {
  0%   {color: red; text-shadow: 0px 3px 0px red; }
  25%  {color: black; text-shadow: 6px 2px 1px red;}
  35%  {color: green; text-shadow: 6px 2px 1px red;}
  40%  {color: red; text-shadow: 3px -3px 2px red; }
  50%  {color: blue; text-shadow: -10px -3px -2px black; }
  65%  {color: red; text-shadow: 4px 7px -0px black; }
  75%  {color: green; text-shadow: -2px 5px 1px black;}
  100% {color: red; text-shadow: 0px 0px 0px red; }
}


