.Mainframe_console__1Nm2r h1 {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  white-space: nowrap; /* Keeps the content on a single line */
  letter-spacing: .15em; /* Adjust as needed */
  -webkit-animation: Mainframe_typing__3hBVA 2.5s steps(120, end);
          animation: Mainframe_typing__3hBVA 2.5s steps(120, end);
}

/* The typing effect */
@-webkit-keyframes Mainframe_typing__3hBVA {
  from { width: 0 }
  to { width: 100% }
}
@keyframes Mainframe_typing__3hBVA {
  from { width: 0 }
  to { width: 100% }
}

.Mainframe_console__1Nm2r h1:after {
  content: ' \258D';
  font-weight: bold;
  -webkit-animation: Mainframe_blink-caret__1oU-5 1.25s infinite;
          animation: Mainframe_blink-caret__1oU-5 1.25s infinite;
}

/* The typewriter cursor effect */
@-webkit-keyframes Mainframe_blink-caret__1oU-5 {
	0% {
		color: transparent;
	}

	45% {
		color: transparent;
	}

	50% {
		color: red;
	}

	95% {
		color: red;
	}

	100% {
		color: transparent;
	}
}
@keyframes Mainframe_blink-caret__1oU-5 {
	0% {
		color: transparent;
	}

	45% {
		color: transparent;
	}

	50% {
		color: red;
	}

	95% {
		color: red;
	}

	100% {
		color: transparent;
	}
}
